.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1.5rem 5rem;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem; 
}

.gpt3__navbar-links_logo img {
    width: 4rem;
    height: 1rem;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p, .gpt3__navbar-sign p, .gpt3__navbar-menu_container p  {
    color: #fff;
    font-weight: 500;
    font-size: 1.1rem;
    line-height: 1.5rem;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.gpt3__navbar-sign button, .gpt3__navbar-menu_container button  {
    color: #fff;
    background-color: #FF4820;

    font-size: .9rem;
    font-weight: 500;
    line-height: 1.5rem;
    padding: .5rem 1.5rem;
    border: none;
    border-radius: .3rem;
    cursor: pointer;
    outline: none;
}

.gpt3__navbar-menu {
    margin-left: 1rem;
    
    display: none;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);

    padding: 2rem;
    position: absolute;
    top: 2.5rem;
    right: 0;
    margin-top: 1rem;
    min-width: 13rem;
    border-radius: .3rem;
    box-shadow: 0 0 5 rgba(0, 0, 0, .2);
}

.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
        display: none;
    }

    .gpt3__navbar-menu {
        display: flex; 
    }
}

@media screen and (max-width: 700px) {
    .gpt3__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__navbar {
        padding: 2rem;
    }

    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 1.25rem;
    }

    .gpt3__navbar-menu_container-links-sign {
        margin-top: 2rem;
        display: block;
    }
}