.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.gpt3__features-container__feature-title {
    flex: 1;
    max-width: 11.25rem;
    margin-right: 2rem;
}

.gpt3__features-container__feature-title h1 {
    font-weight: 800;
    font-size: 1.12rem;
    line-height: 1.5rem;
    letter-spacing: -0.04em;
    color: #fff;
}

.gpt3__features-container__feature-title div {
    width: 2.25rem;
    height: .19rem;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.gpt3__features-container_feature-text {
    flex: 2;
    max-width: 24.4rem;
    display: flex;
}

.gpt3__features-container_feature-text p {
    font-weight: 500;
    font-size: .875rem;
    line-height: 1.5rem;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .gpt3__features-container__feature-title h1 {
        font-size: .875rem;
        line-height: 1.38rem;
    }

    .gpt3__features-container_feature-text p {
        font-size: .75rem;
        line-height: 1.25rem;
    }

    .gpt3__features-container__feature {
        margin: 1rem 0;
    }
}